<template>
  <div class="text-center">
    <v-dialog v-model="dialog" width="530">
      <v-card color="">
        <v-system-bar color="green lighten-1" height="50" dark>
          <v-card-title class="white--text">Advanced Search</v-card-title>
        </v-system-bar>
        <!-- <v-divider></v-divider> -->
        <v-form ref="form">
          <v-container>
            <v-row>
              <v-col cols="" class="mt-3 ml-5 mr-4">
                <v-text-field
                  class=""
                  label="OneEmail or OneID"
                  ref="form"
                  v-model="one_Email"
                  solo
                  rounded
                  background-color="grey lighten-2"
                >
                </v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="" class="ml-5">
                <v-select
                  :items="items"
                  item-text="room_num"
                  rounded
                  label="สถานที่เช็คอิน"
                  solo
                  v-model="checkin_Area"
                  background-color="grey lighten-2"
                ></v-select>
              </v-col>
              <!-- <v-row class="mt-0"> -->
              <v-col cols="" class="mr-4">
                <v-select
                  :items="items"
                  item-text="room_num"
                  rounded
                  label="สถานที่เช็คเอาท์"
                  solo
                  background-color="grey lighten-2"
                  v-model="checkout_Area"
                ></v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="1" class="ml-3">
                <v-checkbox v-model="checkbox_date"></v-checkbox>
              </v-col>
              <v-col cols="5" class="mr-n2 ml-n3">
                <v-menu
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="100px"
                  v-model="menu1"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      background-color="grey lighten-2"
                      prepend-inner-icon="mdi-calendar"
                      :rules="requireRule"
                      v-model="date"
                      solo
                      label="Date"
                      readonly
                      rounded
                      v-bind="attrs"
                      v-on="on"
                      ref="form"
                      v-if="checkbox_date === true"
                    ></v-text-field>
                    <v-text-field
                      background-color="grey lighten-2"
                      prepend-inner-icon="mdi-calendar"
                      :rules="requireRule"
                      solo
                      label="Date"
                      readonly
                      rounded
                      v-bind="attrs"
                      v-on="on"
                      ref="form"
                      disabled
                      v-else-if="checkbox_date === false"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="date"
                    @input="menu1 = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols="1" class="ml-1 mr-n3">
                <v-checkbox v-model="checkbox_month"></v-checkbox>
              </v-col>
              <v-col cols="5" class="">
                <v-menu
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="100px"
                  v-model="menu2"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      background-color="grey lighten-2"
                      prepend-inner-icon="mdi-calendar"
                      :rules="requireRule"
                      v-model="month"
                      solo
                      rounded
                      label="Month"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      ref="form"
                      v-if="checkbox_month === true"
                    ></v-text-field>
                    <v-text-field
                      background-color="grey lighten-2"
                      prepend-inner-icon="mdi-calendar"
                      :rules="requireRule"
                      solo
                      label="Month"
                      readonly
                      rounded
                      v-bind="attrs"
                      v-on="on"
                      ref="form"
                      disabled
                      v-else-if="checkbox_month === false"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    type="month"
                    v-model="month"
                    @input="menu2 = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="" class="ml-5">
                <v-menu
                  ref="form"
                  v-model="menu3"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="time"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="time_in"
                      label="เวลาเข้างาน"
                      prepend-inner-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      solo
                      rounded
                      background-color="grey lighten-2"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-if="menu3"
                    v-model="time_in"
                    full-width
                    @click:minute="$refs.menu.save(time)"
                    @input="menu3 = false"
                  ></v-time-picker>
                </v-menu>
              </v-col>
              <v-col cols="" class="mr-4">
                <v-menu
                  ref="form"
                  v-model="menu4"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="time"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="time_out"
                      label="เวลาออกงาน"
                      prepend-inner-icon="mdi-clock-time-four-outline"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      solo
                      rounded
                      background-color="grey lighten-2"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-if="menu4"
                    v-model="time_out"
                    full-width
                    @click:minute="$refs.menu.save(time)"
                    @input="menu4 = false"
                  ></v-time-picker>
                </v-menu>
              </v-col>
            </v-row>
            

            <!-- </v-row> -->
            <v-row justify="end" class="ma-0">
              <v-col cols="2" class="mb-2 mr-3">
                <v-btn @click="dialog = false" height="48" text>Cancel</v-btn>
              </v-col>
              <v-col cols="2" class="mr-10 mb-2">
                <v-btn
                  @click="getData()"
                  height="48"
                  rounded
                  color="green lighten-1"
                  dark
                  >Search</v-btn
                >
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      checkbox_month: false,
      checkbox_date: false,
      dialog: false,
      one_Email: "",
      checkin_Area: "",
      checkout_Area: "",
      date: "",
      month: "",
      menu1: false,
      menu2: false,
      room_num: false,
      menu_disable: true,
      time_in: null,
      menu3: false,
      time_out: null,
      menu4: false,
    };
  },
  mounted() {
    this.getRoom_num();
  },
  methods: {
    getRoom_num(room_number) {
      console.log(room_number);
      axios
        .get(process.env.VUE_APP_API + "/api/v1/admin/get_room_number", {
          headers: {
            Authorization: "Bearer " + this.parameters_onechat_token,
          },
        })
        .then((respones) => {
          // console.log("result=",result)
          console.log("respones=", respones);
          if (respones.data.message == "success") {
            console.log("data", respones.data.result);
            this.items = respones.data.result;
            console.log("success");
          }
        })
        .catch((error) => {
          // this.spinning = false;
          console.log(error.message);
          // this.loading = false;
          if (error.message === "Request failed with status code 401") {
            console.log("error 401");
            this.dialog = true;
          }
        });
      console.log("result", this.result);
    },
    getData() {
      console.log(this.one_Email);
      this.$emit(
        "messageFromChild",
        this.one_Email,
        this.checkin_Area,
        this.checkout_Area,
        this.date,
        this.month,
        this.time_in,
        this.time_out
      );
      this.$refs.form.reset();
      this.one_Email = "";
      this.checkin_Area = "";
      this.checkout_Area = "";
      this.date = "";
      this.month = "";
      this.checkbox_month = false;
      this.checkbox_date = false;
      this.time_in = ""
      this.time_out = ""
      this.dialog = false;
    },
    openDialog() {
      this.dialog = true;
    },
  },
  watch: {
    checkbox_date(newval) {
      if (newval == true) {
        this.checkbox_month = false;
      }
    },
    checkbox_month(newval) {
      if (newval == true) {
        this.checkbox_date = false;
      }
    },
  },
};
</script>