<template>
  <div class="text-center">
    <v-dialog v-model="dialog" max-width="550">
      <v-card>
        <!-- {{message}} -->
         <v-system-bar
      color="error"
      height="50"
      dark
    >
        <v-card-title class="white--text">Export File</v-card-title>
        <!-- <v-divider></v-divider> -->
         </v-system-bar>
        <v-row class="ma-0">
          <v-col class="mt-6 ml-5" cols="">
            <p>File name:</p>
          </v-col>
          <v-col cols="9" class="mt-4 mr-4">
            <v-text-field
              label="File name"
              v-model="file_name"
              outlined
              rounded
              dense
              ref="form"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mx-8 mb-4 mt-n2">
          <!-- {{ header }}
          {{ amenities }} -->
          <v-chip-group v-model="amenities" column multiple>
            <v-chip
            color="success"
              v-for="name in name"
              :key="name"
              :value="name"
              filter
              outlined
              @click="select_header(name.value)"
            >
              {{ name.text }}
            </v-chip>
            <v-chip filter outlined @click="select_all()" color="success">ทั้งหมด</v-chip>
          </v-chip-group>
        </v-row>
        <v-card-actions class="mt-n2">
          <v-spacer></v-spacer>
          <v-btn
            text
            @click="dialog = false"
            class="mb-3"
          >
            CANCEL
          </v-btn>
          <v-btn
            class="mb-3 mr-3"
            color="error"
            rounded
            dark
            @click="set_message(file_name)"
          >
            DOWNLOAD
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      amenities: [],
      header: [],
      data_list: [],
      dialog: false,
      status_all_button: false,
      name: [
        {
          text: "One Mail",
          value: "one_email",
        },
        {
          text: "OneID",
          value: "one_id",
        },
        {
          text: "เวลาเข้าพื้นที่",
          value: "check_in",
        },
        {
          text: "เวลาออกพื้นที่",
          value: "check_out",
        },
        {
          text: "สถานที่เช็คอิน",
          value: "checkin_area",
        },
        {
          text: "สถานที่เช็คเอาท์",
          value: "checkout_area",
        },
        {
          text: "สถานะ covid tracking",
          value: "covid_tracking",
        },
        {
          text: "วันที่",
          value: "date",
        },
      ],
    };
  },
  props: {
    message: String,
  },
  watch: {
    status_all_button(newValue) {
      if (newValue === true) {
        this.name.forEach((name_button) => {
          // console.log(name_button.value);
          if (!this.header.includes(name_button.value)) {
            console.log(777);
            this.header.push(name_button.value);
          }
          this.amenities.push(name_button);
        });
      } else if (newValue === false) {
        this.header = [];
        this.amenities = [];
      }
    },
  },
  methods: {
    select_all() {
      this.status_all_button = !this.status_all_button;
      // this.header.push("one_email","one_id","check_in","check_out","checkin_area","checkout_area","covid_tracking","date")
    },
    select_header(head) {
      console.log(head);
      if (!this.header.includes(head)) {
        this.header.push(head);
      } else if (this.header.includes(head)) {
        var head_index = this.header.indexOf(head);
        this.header.splice(head_index, 1);
      }
    },
    filter_data(item) {
      const filtered = Object.keys(item)
        .filter((key) => this.header.includes(key))
        .reduce((obj, key) => {
          obj[key] = item[key];
          return obj;
        }, {});
      return filtered;
    },
    openDialogExport() {
      this.dialog = true;
    },
    set_message(file_name) {
      this.message.forEach((items) => {
        this.data_list.push(Object.values(this.filter_data(items)));
      });
      console.log(this.data_list);
      this.export_file(file_name, this.data_list);
    },
    async export_file(file_name, data_list) {
      var body = { head: this.header, data: data_list };
      await axios
        .post(
          process.env.VUE_APP_API +
            "/api/v1/report/export" +
            "?file_name=" +
            file_name,
          body,
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.token_user,
            },
          }
        )
        .then((respones) => {
          // console.log("result=",result)
          console.log("respones=", respones);
          if (respones.statusText == "OK") {
            var file_Name = respones.data.file_name;
            this.file_Path = respones.data.file_path;
            var Name = file_Name.split(".xlsx");
            console.log(Name);
            this.download_file(Name[0], this.file_Path);
            this.dialog = false;
          }
        })
        .catch((error) => {
          // this.spinning = false;
          console.log(error.message);
          // this.loading = false;
          if (error.message === "Request failed with status code 401") {
            console.log("error 401");
            this.dialog = true;
          }
        });
    },
    async download_file(FileName, FilePath) {
      window.open(
        process.env.VUE_APP_API +
          "/api/v1/report/export?file_path=" +
          FilePath +
          "&file_name=" +
          FileName
      );
      this.$refs.form.reset();
      location.reload();
    },
  },
};
</script>